@use '../util/' as *;

html {
    font-size: 100%;
    box-sizing: border-box;
}

*, 
*::before, 
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
}

.container {
    margin: 0 auto;
    max-width: rem(1110);
    padding: 0 rem(25);

    @include breakpoint(large) {
        padding: 0;
    }
}